import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import WorkoutGraph from '../../components/workout/graph';
import WorkoutCalendar from '../../components/workout/calendar';

const initialState = {
	isLoading: false,
};

@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback } = this.props;
        const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		if (team === currentTeam) {
			workoutStore.setCurrentTeam(undefined);
		} else {
			workoutStore.setCurrentTeam(team);
		}
		if (util.isDefined(callback)) {
			callback();
		}
	}

    render() {
        const { userStore, workoutStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = workoutStore;
        return (
			<div class='container-fluid px-0'>
				<div class='row'>
					{teams && teams.map(team => (
						<div class='col-6 mb-2'>
							<div class={`border rounded rounded-pill px-3 py-2 ${currentTeam === team.id ? 'bg-success' : ''}`}
								style='line-height: 0.9em; cursor: pointer; pointer-events: inherit;'
								onClick={() => this.toggleTeam(team.id)}
							>
								<small>
									<i class='fas fa-users mr-2' />
									{team.name}
								</small>
							</div>
						</div>
					))}
				</div>
			</div>
        );
    }
}

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadWorkouts = async () => {
		this.setState({ isLoading: true });
		const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;

		// const currentMonth = new Date().getMonth() + 1;
        // const { month = currentMonth, year = util.getYear(), team = currentTeam, dog = {} } = props;

		await workoutStore.load({ query: { team: currentTeam } });
		await this.loadWorkoutSummary();
		this.setState({ isLoading: false });
	}

	loadDogs = async () => {
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
	}

	async loadTracks() {
		const { trackStore } = this.props.stores;
		await trackStore.load();
	}

	async loadWorkoutSummary() {
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
        await userStore.getWorkoutSummary({ team: currentTeam });
    }

	componentDidMount() {
		this.loadWorkouts();
		this.loadDogs();
		this.loadTracks();
	}

	render() {
		const { isLoading } = this.state;
		const { userStore, appState, workoutStore } = this.props.stores;
		const { month, year } = this.props;
		const { currentTeam } = workoutStore;

		const { darkmode } = appState;
		const workouts = workoutStore.getLatests();
		const {
			workoutSummaryWeek = [],
		} = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 90px;'>
				<div class='row mb-4 fixed-top' style={`margin-top: 58px; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center border-bottom border-primary'>
						<a href='/workouts/' class='text-primary stretched-link'><i class='fas fa-chart-line' /> Oversikt</a>
					</div>
					<div class='col text-center'>
						<a href='/workouts/list' class='text-secondary stretched-link'><i class='fas fa-list' /> Liste</a>
					</div>
					<div class='col text-center'>
						<a href='/workouts/plan' class='text-secondary stretched-link'><i class='fas fa-calendar-alt' /> Plan</a>
					</div>
				</div>
				
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
						<Teams stores={this.props.stores} callback={this.loadWorkouts} />
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-2'>
						<a class='btn btn-primary btn-sm float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> Legg til treningstur
						</a>
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<WorkoutGraph stores={this.props.stores} isLoading={isLoading} />
					</div>
					<WorkoutCalendar stores={this.props.stores} team={currentTeam} basehref={`/workouts/`} month={month} year={year} />
				</div>


				<div class='row'>
					{workouts && workouts.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mt-4'>		
							<h5>Nyeste treningsturer - {workouts.length} stk</h5>
						</div>
						{workouts && workouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} />
						))}
					</div>}
				</div>



				<div class='row'>
					{workoutSummaryWeek && workoutSummaryWeek.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5 class='mt-4'>Treningsoversikt</h5>
						</div>
						{/* {JSON.stringify(workoutSummaryWeek)} */}
						{workoutSummaryWeek && workoutSummaryWeek.reverse().map(summary => (
							<WorkoutSummary stores={this.props.stores} summary={summary} />
						))}
					</div>}
				</div>
			</div>
		);
	}
}

export default Workouts;
